<template>
  <div class="my-lawler">
    <v-container>
      <v-row class="text-center bg-primary-gradient">
        <v-col cols="12">
          <v-img
              :src="require('../../assets/logo-white.png')"
              contain
              height="120"
          />
        </v-col>

        <v-col cols="12" align-self="center">
          <h1 style="font-size: 1.5rem" class="font-weight-bold profile-title text-center">Documentos en línea</h1>
        </v-col>
      </v-row>

      <v-row class="breadcrumbs">
        <v-col cols="12">Categoría > Subcategoría > Tipo de pago > Depósito</v-col>
      </v-row>

      <v-row>
        <v-col>
          <div class="page-title text-capitalize">{{ operation.name }}</div>

          <div class="call-to-action">
            <div class="text text-center" style="display: block">Precio final: S/. {{ operation.price_final }}</div>
          </div>


          <div class="page-title">Depósito bancario</div>

          <div v-for="bank in banks" :key="bank.id" class="call-to-action">
            <v-img
                :src="bank.iconUrl"
                contain position="left"
                width="60"
            />
            <span>{{ bank.type }}:</span> {{ bank.number }}
            <div style="display: inline-grid; text-align: center">
              <v-btn
                class="mx-2"
                fab
                dark
                small
                color="primary"
                @click="onCopy(bank.number)"
              >
                <v-icon dark> mdi-clipboard-outline</v-icon>
              </v-btn>
              <span>Copiar</span>
            </div>
            <br>
            <span>Titular:</span> {{ bank.principal }}
          </div>

          <div class="text-center" style="margin: 2rem 0">
            <v-btn color="primary" width="160" @click="goToConfirm()">
              Continuar
              <v-img :src="require('../../assets/icons/lawyer-04.svg')" height="20" contain/>
            </v-btn>
          </div>

        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import {mapGetters} from "vuex";
import axios from "axios";

export default {
  name: 'DocumentsBank',
  data: () => ({
    isLoading: false,
    banks: [],
  }),
  computed: {
    ...mapGetters(['user', 'operation']),
  },
  mounted() {
    this.getBankAccounts();
  },
  methods: {
    goToConfirm() {
      this.isLoading = true

      let url = 'operations';
      let data = {
        resource_id: this.operation.id,
        payment_method_id: 2,
        status_id: 3,
        coupon_code: this.operation.coupon_code,
      }

      axios.post(url, data).then((response) => {
        this.$router.push({name: 'OperationPayment', params: {id: response.data.id}})
      }).catch(() => {
        this.notyf.error('Ups! Ocurrio un error, por favor intentelo mas tarde.');
      }).then(() => {
        this.isLoading = false
      })
    },
    getBankAccounts() {
      axios.get('banks').then((response) => {
        this.banks = response.data;
      })
    },
    onCopy(number) {
      navigator.clipboard.writeText(number);
    }
  }
}
</script>

<style lang="scss" scoped>
.call-to-action {
  span {
    color: #184C72
  }
}
</style>